import { Avatar, IconButton, useMediaQuery } from '@mui/material'
import * as React from 'react'
import SwiperCore, { Navigation, Pagination } from 'swiper'

import { Swiper, SwiperSlide } from 'swiper/react'

import useStyles from './swiped-cards.style'

import imgPhoto1 from '../../../../assets/landing/photo-1.png'
import imgPhoto2 from '../../../../assets/landing/photo-2.png'
import imgPhoto3 from '../../../../assets/landing/photo-3.png'

SwiperCore.use([Pagination, Navigation])

interface AvaCardProps {
  name?: string
  position?: string
  text?: string
  image?: string
  button?: React.ReactNode
}

const AvaCard = ({ image, name, position, text, button }: AvaCardProps) => {
  const classes = useStyles()
  return (
    <div className={classes.avaCard}>
      <div className={classes.avaHeader}>
        <Avatar className={classes.ava} src={image} />
        <div className={classes.avaInfo}>
          <h3 className={classes.avaName}>{name}</h3>
          <p className={classes.avaPosition}>{position}</p>
        </div>
      </div>
      <p className={classes.avaText}>{text}</p>
      {button}
    </div>
  )
}

const SwipedCards = () => {
  const classes = useStyles()
  const swiperRef = React.useRef<SwiperCore>()
  const matches1400 = useMediaQuery('(min-width:1400px)')
  const matches768 = useMediaQuery('(min-width:768px)')
  const matches500 = useMediaQuery('(max-width:500px)')
  // eslint-disable-next-line no-nested-ternary
  const slidesNumber = matches1400 ? 3 : matches768 ? 2 : 1

  const prevButton = (
    <IconButton className={`${classes.swiperNavPrev}`} onClick={() => swiperRef.current?.slidePrev()}>
      <svg width="36" height="36" viewBox="0 0 36 36" fill="none" xmlns="http://www.w3.org/2000/svg">
        <path
          d="M23.3661 8.90202C23.366 9.29982 23.2079 9.68129 22.9266 9.96252L16.0476 16.8415C15.7738 17.1267 15.621 17.5067 15.621 17.902C15.621 18.2973 15.7738 18.6773 16.0476 18.9625L22.9266 25.8415C23.1998 26.1244 23.351 26.5033 23.3476 26.8966C23.3442 27.2899 23.1864 27.6661 22.9083 27.9443C22.6302 28.2224 22.254 28.3801 21.8607 28.3835C21.4674 28.387 21.0885 28.2358 20.8056 27.9625L13.9266 21.0835C13.1084 20.2261 12.6519 19.0865 12.6519 17.9013C12.6519 16.7161 13.1084 15.5765 13.9266 14.719L20.8056 7.84152C21.0154 7.63181 21.2826 7.489 21.5735 7.43115C21.8645 7.3733 22.166 7.403 22.4401 7.51651C22.7141 7.63001 22.9483 7.82222 23.1132 8.06884C23.278 8.31545 23.366 8.6054 23.3661 8.90202Z"
          fill="#797981"
        />
      </svg>
    </IconButton>
  )

  const nextButton = (
    <IconButton className={`${classes.swiperNavNext}`} onClick={() => swiperRef.current?.slideNext()}>
      <svg width="36" height="36" viewBox="0 0 36 36" fill="none" xmlns="http://www.w3.org/2000/svg">
        <path
          d="M13.07 7.19738C12.7888 7.47893 12.6309 7.86073 12.6309 8.25883C12.6309 8.65693 12.7888 9.03874 13.07 9.32028L19.949 16.2054C20.2228 16.4909 20.3756 16.8712 20.3756 17.2669C20.3756 17.6625 20.2228 18.0429 19.949 18.3283L13.07 25.2135C12.7968 25.4966 12.6456 25.8759 12.649 26.2695C12.6524 26.6632 12.8102 27.0397 13.0883 27.3181C13.3664 27.5964 13.7426 27.7543 14.1359 27.7578C14.5292 27.7612 14.9081 27.6098 15.191 27.3364L22.07 20.4527C22.488 20.0345 22.8195 19.5379 23.0457 18.9914C23.2719 18.4449 23.3883 17.8592 23.3883 17.2676C23.3883 16.6761 23.2719 16.0903 23.0457 15.5438C22.8195 14.9973 22.488 14.5008 22.07 14.0825L15.191 7.19738C14.9097 6.91593 14.5283 6.75781 14.1305 6.75781C13.7328 6.75781 13.3513 6.91593 13.07 7.19738Z"
          fill="#797981"
        />
      </svg>
    </IconButton>
  )

  const mobileNextButton = matches500 ? (
    <IconButton className={`${classes.avaButtonMobile}`} onClick={() => swiperRef.current?.slideNext()}>
      <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
        <path
          d="M8.71271 4.80086C8.52524 4.98856 8.41992 5.24309 8.41992 5.50849C8.41992 5.77389 8.52524 6.02843 8.71271 6.21613L13.2987 10.8062C13.4812 10.9965 13.5831 11.2501 13.5831 11.5139C13.5831 11.7776 13.4812 12.0312 13.2987 12.2215L8.71271 16.8116C8.53055 17.0004 8.42976 17.2532 8.43203 17.5156C8.43431 17.778 8.53948 18.0291 8.72489 18.2147C8.9103 18.4002 9.16111 18.5055 9.42331 18.5078C9.6855 18.5101 9.93811 18.4092 10.1267 18.2269L14.7127 13.6378C14.9913 13.3589 15.2124 13.0279 15.3632 12.6635C15.514 12.2992 15.5916 11.9087 15.5916 11.5144C15.5916 11.12 15.514 10.7295 15.3632 10.3652C15.2124 10.0008 14.9913 9.66979 14.7127 9.39096L10.1267 4.80086C9.93918 4.61322 9.68487 4.50781 9.41971 4.50781C9.15454 4.50781 8.90024 4.61322 8.71271 4.80086Z"
          fill="#1D6BF3"
        />
      </svg>
    </IconButton>
  ) : null

  return (
    <div className={classes.root}>
      {!matches500 && prevButton}
      <Swiper
        modules={[Navigation, Pagination]}
        spaceBetween={20}
        slidesPerView={slidesNumber}
        className={classes.swiper}
        onBeforeInit={(swiper) => {
          swiperRef.current = swiper
        }}
      >
        <SwiperSlide>
          <AvaCard
            image={imgPhoto1}
            button={mobileNextButton}
            name="Андрей"
            position="Python"
            text="Хочу сказать, что ребята из Практикума очень помогли составить резюме и поддерживали меня советами по поводу всех переживаний. Очень рад, что решил изменить свою карьеру!"
          />
        </SwiperSlide>
        <SwiperSlide>
          {' '}
          <AvaCard
            image={imgPhoto2}
            button={mobileNextButton}
            name="Вячеслав"
            position="Инженер по тестированию"
            text="На сегодняшний день мне активно звонят работодатели, с целью предложить свои вакансии. А я пока не могу выбрать)) Приятно, черт возьми!"
          />
        </SwiperSlide>
        <SwiperSlide>
          {' '}
          <AvaCard
            image={imgPhoto3}
            button={mobileNextButton}
            name="Анна"
            position="Менеджер проектов"
            text="Очень сильно помогала обратная связь от кураторов. Многие учебные техники уже удалось применить на практике"
          />
        </SwiperSlide>
        <SwiperSlide>
          {' '}
          <AvaCard
            image={imgPhoto1}
            button={mobileNextButton}
            name="Андрей"
            position="Python"
            text="Хочу сказать, что ребята из Практикума очень помогли составить резюме и поддерживали меня советами по поводу всех переживаний. Очень рад, что решил изменить свою карьеру! "
          />
        </SwiperSlide>
      </Swiper>
      {!matches500 && nextButton}
    </div>
  )
}

export default SwipedCards
